import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import Wish from './wish.jsx'

import { fetchWishes, updateWishBought} from '../../actions/wish_actions';
import Loading from '../helpers/loading';

class Wishes extends Component {

    constructor(props) {
        super(props);
        this.props.fetchWishes();
        this.state = {
            message: '',
        };
    }
    render() {
        if (!this.props.wishes) {
            return <Loading />;
        }
        console.log(this.props.wishes.val())

        return (
            <div className="">
                <form id="frmProfile" role="form" onSubmit={this.onFormSubmit}>
                    <h2>nskeliste i annledning bryllup 1. Juli 2017</h2>
                    <p>{this.state.message}</p>
                    <br />
                    <ul className="list-unstyled">
                      {Object.keys(this.props.wishes.val()).map((wish) => <Wish reserve={this.props.updateWishBought} id={wish} key={wish} wish={this.props.wishes.val()[wish]} />)}
                    </ul>
                </form>
            </div>
        );
    }

}


function mapDispatchToProps(dispatch) {
    return bindActionCreators({ fetchWishes, updateWishBought}, dispatch);
}


function mapStateToProps(state) {
    return { wishes: state.wishes };
}


export default connect(mapStateToProps, mapDispatchToProps)(Wishes);



