import React, { Component } from 'react';

class WishButton extends Component {
  constructor(props) {
    super(props);
    this.state = {
      message: '',
    };
    this.reserveOne = this.reserveOne.bind(this);
    this.reserveMultiple = this.reserveMultiple.bind(this);
    this.reserve = props.reserve;
  }
  reserveOne(event){
    event.preventDefault();
    this.reserve(this.props.id, 1 + this.props.boughtCount);
  }
  reserveMultiple(event){
    event.preventDefault();
    this.reserve(this.props.id, parseInt(this.refs.count.value) + parseInt(this.props.boughtCount));
  }

  render(){
    if (this.props.wishCount == this.props.boughtCount) {
      return <div></div>;
    } else if (this.props.wishCount == 1) {
      return <button type="submit" className="btn btn-default" onClick={this.reserveOne}>Marker denne som kjpt</button>;
    } else {
      return <div>Marker <input type="number" ref="count" className="wish-counter" step="1" min="0"
                                max={this.props.wishCount - this.props.boughtCount}/>
        <button type="submit" className="btn btn-default" onClick={this.reserveMultiple}>som kjpt</button>
      </div>;
    }
  }

}

export default WishButton;



