import React, { Component } from 'react';
import { browserHistory } from 'react-router';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { addWish } from '../../actions/wish_actions';

class AddWish extends Component {
  constructor(props) {
    super(props);
    this.onFormSubmit = this.onFormSubmit.bind(this);
    this.state = {
      message: '',
    };
  }

  onFormSubmit(event) {
    event.preventDefault();
    const item = {
      name: this.refs.name.value,
      description: this.refs.description.value,
      url: this.refs.url.value,
      imageUrl: this.refs.imageUrl.value,
      wishCount: this.refs.wishCount.value,
      boughtCount: 0,
    };
    console.log("adding: " + item);
    this.props.addWish(item).then((data) => {
        if (data.payload.errorCode) {
          console.log(data.payload);
          this.setState({ message: data.payload.errorMessage })
          ;
        } else if (this.refs.addnew.value === 'dsf') {
          this.state = {
            message: '',
          };
        } else {
          console.log(this.refs.addnew.value)
          browserHistory.push('/');
        }
      }
    );
  }

  render() {
    return (
      <div className="col-md-4">
        <form id="frmRegister" role="form" onSubmit={this.onFormSubmit}>
          <p>{this.state.message}</p>
          <h2>Register</h2>
          <div className="form-group">
            <label htmlFor="txtName">Navn</label>
            <input
              type="text" className="form-control" ref="name" id="txtName" placeholder="Enter name"
              name="name"
            />
          </div>
          <div className="form-group">
            <label htmlFor="txtDescription">Beskrivelse</label>
            <input
              type="text" className="form-control" ref="description" id="txtDescription" placeholder="Enter description"
              name="description"
            />
          </div>
          <div className="form-group">
            <label htmlFor="txtUrl">Url</label>
            <input
              type="url" className="form-control" ref="url" id="txtUrl" placeholder="Enter url"
              name="url"
            />
          </div>
          <div className="form-group">
            <label htmlFor="txtImageUrl">Bilde</label>
            <input
              type="url" className="form-control" ref="imageUrl" id="txtImageUrl" placeholder="Enter image url"
              name="imageUrl"
            />
          </div>
          <div className="form-group">
            <label htmlFor="addNew">Legg til ny? </label>
            <input
              type="checkbox" className="form-control" ref="addnew" id="addNew" placeholder="Add new"
              name="addnew"
            />
          </div>
          <div className="form-group">
            <label htmlFor="txtWishCount">Antall</label>
            <input
              type="number" className="form-control" ref="wishCount" id="txtWishCount" placeholder="Enter desiered count"
              name="wishCount"
            />
          </div>
          <button type="submit" className="btn btn-default" name="register">Register</button>
          <button type="submit" className="btn btn-default" name="new">Register and add new</button>

        </form>
      </div>
    );
  }

}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({
    addWish,
  }, dispatch);
}

function mapStateToProps(state) {
  return { currentUser: state.currentUser };
}

export default connect(mapStateToProps, mapDispatchToProps)(AddWish);



