import './bundle.scss';
import React from 'react';
import WishButton from './wishButton'


export default (props) => (
  <li id={props.id} className="wish-container">
    <img src={props.wish.imageUrl} className="img-rounded wish-image"/>
    <div className="">
      <h3 >
        {props.wish.name}
      </h3>
      <p>{props.wish.description}.&nbsp;
        <a href={props.wish.url} className="wish-url" target="_blank">Se i en nettbutikk</a></p>
      <p>nskes: {props.wish.wishCount}, Allerede kjpt: {props.wish.boughtCount}</p>

      <WishButton reserve={props.reserve} id={props.id} wishCount={props.wish.wishCount} boughtCount={props.wish.boughtCount}/>
    </div>
  </li>
);



