import FireBaseTools from '../utils/firebase';
import {
  FETCH_WISHES,
  ADD_WISH,
  UPDATE_WISH_COUNT
} from './types';

export function fetchWishes() {
  const request = FireBaseTools.getDatabaseReference("wishes").orderByValue().once('value');

  return {
    type: FETCH_WISHES,
    payload: request,
  };
}

export function addWish(wish) {
  var request = FireBaseTools.getDatabaseReference("wishes").push(wish);
  return {
    type: ADD_WISH,
    payload: request,
  };
}
export function updateWishBought(id, count) {
  console.log("updating with " + id +  " " + count)
  var request = FireBaseTools.getDatabaseReference("wishes/" + id ).update({boughtCount:count});
  return fetchWishes();
}




